import myScrollX from '@/components/my-scrollX/my-scrollX.vue'
export default {
	components: {
		myScrollX
	},
	data() {
		return {
			scrollTopList:[{
				typeName: '推荐',
				id: ''
			}],
			currentTopTab: 0,
			beforeColor: '#999999',
			afterColor: '#ffffff',
			articleList:[],
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '- 暂时没有新内容了呢 -'
			},
			status: 'loading',
			code:'',
			memberId:'',
			typeId:'',
			articleName:'',
			page:1,
		}
	},
	onPullDownRefresh() {
		this.page = 1;
		this.articleList = []
		this.scrollTopList = [{
			typeName: '推荐',
			id: ''
		}]
		uni.showLoading({
			title: '加载中'
		});
		this.getTrainList()
		uni.hideLoading();
		uni.stopPullDownRefresh()
	},
	onReachBottom(){
		this.scrollTopList = [{
			typeName: '推荐',
			id: ''
		}]
		if (this.code != '-116') {
			this.page = this.page + 1;
			this.getTrainList();
		}
	},
	onShow(){
		this.page = 1;
		this.articleList = []
		this.scrollTopList = [{
			typeName: '推荐',
			id: ''
		}]
		this.getTrainList()
	},
	methods:{
		// 获取首页数据列表
		getTrainList(){
			this.memberId = uni.getStorageSync('userInfo') ? JSON.parse(uni.getStorageSync('userInfo')).id : ''
			this.$Request.get('/appArticleController.do?getArticleList',{
				memberId: this.memberId,
				typeId: this.typeId,
				articleName: this.articleName,
				page: this.page
			}).then(res => {
				this.code = res.code
				this.status = 'noMore'
				this.scrollTopList = this.scrollTopList.concat(res.data.typeList).map(item => {
					return {
						...item,
						groupName: item.typeName
					}
				})
				if(res.code == 0){
					this.articleList =  [...this.articleList, ...res.data.articleList].map(item => {
						return {
							...item,
							createDate: item.createDate && item.createDate.substring(0,10)
						}
					})
				}else if(res.code == '-118' || res.code == '-116'){
					this.status = 'noMore'
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		// 收藏
		clickAttention(item, index){
			let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
			let articleId = item.id
			this.$Request.get(`/appAttentionController.do?takeArticleAttention&memberId=${memberId}&articleId=${articleId}`)
			.then(res => {
				if(res.code == 0){
					this.articleList[index].attentionNum = item.attentionType == 1 ? item.attentionNum - 1 : item.attentionNum + 1
					this.articleList[index].attentionType = item.attentionType == 1 ? 0 : 1
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		bindNameInput(e){
			this.articleName = e.target.value
			console.log(e.target.value)
			this.articleList = []
			this.page = 1
			this.getTrainList()
		},
		clickSearch(){
			this.articleList = []
			this.page = 1
			this.getTrainList()
		},
		tabChange(item, index){
			this.currentTopTab = index
			this.typeId = item.id
			this.articleList = []
			this.scrollTopList = [{
				typeName: '推荐',
				id: ''
			}]
			this.page = 1
			this.getTrainList()
		},
		gotoUrl(item){
			uni.navigateTo({
				url: `/pages/discover/discoverDetail?id=${item.id}`
			})
		}
	}
}
